.divider{
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    /* padding: 20px 10px; */
}
.divider p{
    display: block;
    flex: 0.7 1;
    padding: 0;
    margin-top: 25px;
    font-size: 26px;
    font-weight: 600;
}
.left-line{
    border:0.1px solid rgba(18, 18, 18, 0.35);
    flex: 1 1;
}
.right-line{
    border:0.1px solid rgba(18, 18, 18, 0.35);
    flex: 1 1;
}


.category-container{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-gap: 2rem;
    padding:20px;
    height:auto;
}
.loader-box{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
  }


@media screen and (max-width: 560px) {
    .category-container{
        width: 100%;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(100%, 1fr));
        grid-gap: 2rem;
        padding:20px;
        height:auto;
    }
}
